const form = document.querySelector("#form");
const submitBtn = document.querySelector("#submit-btn");
const backdrop = document.querySelector("#backdrop");
const closeBtn = document.querySelector("#close-btn");
const page = document.querySelector("body");
const user = {};
import { makeUserProperty } from "./make-user.js";

form.addEventListener("submit", (e) => {
  e.preventDefault();
  makeUserProperty(
    form.querySelector("#form-name"),
    form.querySelector("#form-name").dataset.property,
    user
  );
  makeUserProperty(
    form.querySelector("#form-email"),
    form.querySelector("#form-email").dataset.property,
    user
  );
  makeUserProperty(
    form.querySelector("#form-message"),
    form.querySelector("#form-message").dataset.property,
    user
  );
  form.querySelector("#form-name").value = "";
  form.querySelector("#form-email").value = "";
  form.querySelector("#form-message").value = "";
  backdrop.classList.toggle("is-hidden");
  page.classList.toggle("no-scroll");
});

closeBtn.addEventListener("click", () => {
  backdrop.classList.toggle("is-hidden");
  page.classList.toggle("no-scroll");
});

